@charset "utf-8";

.project h3{
  text-align: center;
  margin-top:70px;
  font-size: 32px;
}

.movie_list{
  display:flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top:50px;
}

.movie_list .movieList{
  width: 230px;
  padding-bottom:50px;
}
.movie_list .movieList h4,
.movie_list .movieList p{
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}