@charset "utf-8";

*{margin:0; padding:0;}

.header{
  width: 100%; height: 80px;
  background: #ff0;
  text-align: center;
}
.header nav{
  width: 1200px;
  margin: 0 auto;
}
.header nav ul{
  display: flex;
  justify-content: space-around;
}
.header nav ul li{
  margin: 10px 20px;
  list-style:none;
}
.header nav ul li a{
  color:#333;
  text-decoration: none;
}
.header nav ul li a:hover{
  font-weight: bold;
}

main{
  width: 1050px;
  margin: 0 auto;
}

.footer{
  width: 100%; line-height: 40px;
  background: #333;
  color:#fff;
}
.footer address{
  font-style: normal;
  text-align: center;
}